	export default {
		data() {
			return {
				style: {
					pageHeight: 0,
					contentViewHeight: 0
				},
                username: ''
			}
		},
        methods:{
            checkName(){
                if (this.username === '') {
                    uni.showToast({
                        title: '昵称不能为空',
                        icon: 'none',
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    return false
                } else{
                    return true
                }
            }
        },
        onLoad() {
            const info = uni.getSystemInfoSync();
            this.style.pageHeight = info.windowHeight;
            this.style.contentViewHeight = info.windowHeight; //像素
            this.username = this.$store.state.userInfo.userName;
        },
        onNavigationBarButtonTap() {
            if (!this.checkName()) {
                return
            }
            let userinfo = this.$store.state.userInfo;
            let userid = userinfo.userId;
            let usertoken = userinfo.loginToken;
            uni.request({
                url: this.websiteUrl + '/api/user/updUnick',
                method: 'POST',
                header: {
                    Authorization: usertoken
                },
                data: {
                    userId: userid,
                    userNickName: this.username
                },
                success: res => {
                    if (res.data.status == 0) {
                        userinfo.userName = res.data.user.userNickName;
                        this.$store.commit('login',userinfo);
                        uni.showToast({
                            title: '修改成功',
                            mask: true
                        });
                        setTimeout(function(){
                            uni.hideToast();
                            uni.navigateBack();
                        },400)
                    } else if (res.data.status == 1001) {
                        uni.showToast({
                            title: res.data.message,
                            icon: 'none',
                            mask: true
                        });
                        setTimeout(function () {
                            uni.hideToast();
                            uni.navigateTo({
                                url:"/pages/login/login",
                                animationType:"slide-in-right"
                            });
                        }, 300)
                    } else {
                        uni.showToast({
                            title: res.data.message,
                            icon: 'none',
                            mask: true
                        });
                        setTimeout(function(){
                            uni.hideToast();
                        },2000)
                    }
                },
                fail: () => {},
                complete: () => {}
            });
        }
	}
